import { makeTodoAndAppendIt } from "./index.js";
import { makeTodoObject } from "./todoDetailsMaker.js";
import { appendTodoDetails } from "./todoDetailsMaker.js";


export function handleProjects(projectsContainer){

    if(localStorage.getItem("projects")){
        appendProjects(projectsContainer)
    } else {
        let projects = [];
        let defaultProject = {
            projectName: "For University",
            todos: ['Play some football', 'Studying', 'Have Some fun']
        }
        projects.push(defaultProject);
        
        localStorage.setItem("projects", JSON.stringify(projects));
        appendProjects(projectsContainer);
    }
}




export function appendProjects(projectsContainer){
    const projs = JSON.parse(localStorage.getItem("projects"));
    const projects = [...projs]
    
    projects.forEach((project) => {

       

       projectsContainer.appendChild(handleTheProjectAndTodosDOM(project));

    })
}


export function makeProjectObjects(projectName, todos){

    return {
        projectName,
        todos
    }
}


export function handleTheProjectAndTodosDOM(project, projectss, mainContentPage){

        let isTodoInfoFormShown = false;
        let lastSelectedTodo;

        const projectContainer = document.createElement("div");
        projectContainer.classList.add("project")

        const projHeader = document.createElement("div");
        projHeader.classList.add("project-title-remove-project");

        const projTitle = document.createElement("h2");
        projTitle.innerText = project.projectName;
        projTitle.classList.add("project-title")

        const removeProjBtn = document.createElement("span");
        removeProjBtn.setAttribute("class", "material-symbols-outlined");
        removeProjBtn.setAttribute("id", "remove-project-icon");
        removeProjBtn.innerText = 'remove';
       
        removeProjBtn.addEventListener("click", (e) => {
            const projs = [...JSON.parse(localStorage.getItem("projects"))];
            let indexOfProject = projs.findIndex(proj => proj.projectName === project.projectName);
            projs.splice(indexOfProject, 1);
            console.log(projs[0] === undefined)
            if(projs[0] === undefined){
                localStorage.removeItem("todosDetails")
                localStorage.removeItem("lastSelectedTodo");
                mainContentPage.replaceChildren();
                // localStorage.removeItem("todosDetails")
            }
            if(project.todos.includes(localStorage.getItem("lastSelectedTodo"))){
                localStorage.removeItem("lastSelectedTodo");
                mainContentPage.replaceChildren();
            }
            localStorage.setItem("projects", JSON.stringify(projs))
            console.log(indexOfProject)
            projectss.removeChild(projectContainer);
        });
        projHeader.append(projTitle, removeProjBtn);


        const toDos = document.createElement("div");
        toDos.classList.add("todos");


        let todoItems = project.todos;

        todoItems.forEach((todo) => {
            const todoContainer = document.createElement("div");
            todoContainer.classList.add("todo-item");
            const checkBox = document.createElement("div");
            checkBox.classList.add("check-box");
            const todoTitle = document.createElement("div");
            todoTitle.classList.add("check-box-title");

            todoTitle.innerText = todo;

            todoTitle.addEventListener("click", (event) => {
                console.log("x")
                if(localStorage.getItem("todosDetails")){
                    let allTodosDetails = [...JSON.parse(localStorage.getItem("todosDetails"))];
                    let indexOdThisTodo = allTodosDetails.findIndex(todoDetailsObject => {
                        return todoDetailsObject.todoTitle === todo
                    });
                        
                        if(indexOdThisTodo !== -1){
                            let thisTodoDetails = allTodosDetails[indexOdThisTodo];

                            appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContentPage); 
                            lastSelectedTodo = thisTodoDetails.todoTitle;
                            localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
                        } else {
                            let thisTodoDetails = makeTodoObject(todo, "");
                            allTodosDetails.push(thisTodoDetails);

                            localStorage.setItem("todosDetails", JSON.stringify(allTodosDetails))
                            appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContentPage);
                            lastSelectedTodo = thisTodoDetails.todoTitle;
                            localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
                        }

                        

                       

                } else {
                    let allTodosDetails = [];
                    let thisTodoDetails = makeTodoObject(todo, "");
                    allTodosDetails.push(thisTodoDetails);
                    localStorage.setItem("todosDetails", JSON.stringify(allTodosDetails));
                    appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContentPage);
                    lastSelectedTodo = thisTodoDetails.todoTitle;
                    localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
                    
                }
            });

            todoContainer.append(checkBox, todoTitle);
            toDos.append(todoContainer)
        })

        const insertTodoContainer = document.createElement("div");
        insertTodoContainer.classList.add("todo-item");
        insertTodoContainer.classList.add("todo-item-info");

        const cancle = document.createElement("div")
        cancle.classList.add("cancle");
        const cancleBtnDiv = document.createElement("div")
        cancleBtnDiv.classList.add("cancle-icon");
        const cancleBtn = document.createElement("span");
        cancleBtn.classList.add("material-symbols-outlined", "cancle-todo-btn");
        cancleBtn.innerText = 'close';

        cancleBtn.addEventListener("click", (e) => {
            toDos.querySelector(".todo-item-info").classList.remove("show")
            isTodoInfoFormShown = false;
        })
        cancleBtnDiv.appendChild(cancleBtn)
        cancle.appendChild(cancleBtnDiv)

        const todoTitleInputDiv = document.createElement("div")
        todoTitleInputDiv.classList.add("todo-title")

        const todoTitleInput = document.createElement("input");
        todoTitleInput.classList.add("todo-title-input");
        todoTitleInput.setAttribute("type", "text");
        todoTitleInput.setAttribute("placeholder", "To-Do Title");

        todoTitleInput.addEventListener("keydown", (e) => {

            if(e.key === "Escape"){
                toDos.querySelector(".todo-item-info").classList.remove("show")
                todoTitleInput.value = "";
                isTodoInfoFormShown = false;
            } 

            if(e.key === "Enter" && todoTitleInput.value.trim() !== ""){
                todoItems.push(todoTitleInput.value);
                
                
                let projss = [...JSON.parse(localStorage.getItem("projects"))];
                let indexOfProject = projss.findIndex(proj => proj.projectName === project.projectName);
                console.log(indexOfProject);
                projss.splice(indexOfProject, 1, project);
                localStorage.setItem("projects", JSON.stringify(projss))
                makeTodoAndAppendIt(toDos, todoTitleInput.value, toDos.querySelector(".todo-item-info"))
                toDos.querySelector(".todo-item-info").classList.remove("show")
                todoTitleInput.value = "";
                isTodoInfoFormShown = false;
            }
            


        });

        todoTitleInputDiv.appendChild(todoTitleInput);

        insertTodoContainer.append(cancle, todoTitleInputDiv);

        

        const addTodoBtnDiv = document.createElement("div")
        addTodoBtnDiv.classList.add("add-todo-item");

        addTodoBtnDiv.addEventListener("click", (e) => {
            toDos.querySelector(".todo-item-info").classList.add("show")
            isTodoInfoFormShown = true;
        });


        const checkBox = document.createElement("div");
        checkBox.classList.add("check-box")

        const addIcon = document.createElement("span");
        addIcon.classList.add("material-symbols-outlined");
        addIcon.innerText = 'add';

        checkBox.appendChild(addIcon);


        const addBtnText = document.createElement("div")
        addBtnText.classList.add("check-box-title");
        addBtnText.innerText = "Todo";

        addTodoBtnDiv.append(checkBox, addBtnText);

        toDos.append(insertTodoContainer, addTodoBtnDiv);

        projectContainer.append(projHeader, toDos)

        return projectContainer;
    
}