export function appendTodoDetails(todoTitle, todoDetails, contentPage){

    const title = document.createElement("h2");
    title.innerText = todoTitle;
    title.classList.add("title-for-todo");

    const details = document.createElement("textarea");
    details.setAttribute("id", "todo-details")
    details.setAttribute("placeholder", "type details about the todo....");
    details.innerText = todoDetails;
    
    if(localStorage.getItem("todosDetails")){
        
        let allTodosDetails = [...JSON.parse(localStorage.getItem("todosDetails"))];
        let indexOdThisTodo = allTodosDetails.findIndex(todoDetailsObject => {
            return todoDetailsObject.todoTitle === todoTitle;
        });
    
        
        if(indexOdThisTodo !== -1){
            details.style.height = allTodosDetails[indexOdThisTodo].textAreaHeight;
        } else {
            console.log("skipped")
        }

    }
   
     

    details.addEventListener('input', () => {
    

        details.style.height = 'auto';
        details.style.height = details.scrollHeight + 'px';
 
        console.log(details.style.height)

        let allTodosDetails = [...JSON.parse(localStorage.getItem("todosDetails"))];
        let indexOdThisTodo = allTodosDetails.findIndex(todoDetailsObject => {
            return todoDetailsObject.todoTitle === todoTitle;
        });

        allTodosDetails[indexOdThisTodo].todoNotes = details.value;
        allTodosDetails[indexOdThisTodo].textAreaHeight = details.style.height;
        allTodosDetails.splice(indexOdThisTodo, 1, allTodosDetails[indexOdThisTodo]);

        localStorage.setItem("todosDetails", JSON.stringify(allTodosDetails));
        
    
    });
    
    contentPage.replaceChildren(title, details)
    
}

export function makeTodoObject(todoTitle, todoNotes, textAreaHeight = "40px"){
    
    return {

            todoTitle,
            todoNotes,
            textAreaHeight
    }
    
}