import "./styles.css";
import { handleTheProjectAndTodosDOM } from "./projects.js";
import { makeProjectObjects } from "./projects.js";
import { appendTodoDetails } from "./todoDetailsMaker.js";
import { makeTodoObject } from "./todoDetailsMaker.js";

const projectsContainer = document.querySelector(".projects")
const sideBar = document.querySelector(".side-bar")
const sideBarMenu = document.querySelector(".side-bar-menu")
const hideSideBar = document.querySelector("#slide-in");
const mainContent = document.querySelector(".tasks-page");
const projectPopUp = document.querySelector(".add-project-popup")
const hideProjectPopUpBtn = document.querySelector("#cancle-project-btn");
const overlay = document.querySelector(".overlay");
const addProjectBtn = document.querySelector("#add-project-icon");
const saveProjectBtn = document.querySelector("#save-project-btn");
const projectNameInput = document.querySelector("#project-name-input");
// const textarea = document.getElementById('todo-details');





addProjectBtn.addEventListener("click", (e) => {
    overlay.classList.add("show");
    projectPopUp.classList.add("show");
});

hideProjectPopUpBtn.addEventListener("click", (e) => {
    overlay.classList.remove("show");
    projectPopUp.classList.remove("show");
    projectNameInput.value = "";    
});

overlay.addEventListener("click", (e) => {
    overlay.classList.remove("show");
    projectPopUp.classList.remove("show");
});




export function makeTodoAndAppendIt(parent, todoTitleInput, addTodoBtn){
    
    const todoContainer = document.createElement("div");
    todoContainer.classList.add("todo-item");
    const checkBox = document.createElement("div");
    checkBox.classList.add("check-box");
    const todoTitle = document.createElement("div");
    todoTitle.classList.add("check-box-title");

    todoTitle.innerText = todoTitleInput;

    todoTitle.addEventListener("click", (event) => {
        console.log("x")
        if(localStorage.getItem("todosDetails")){
            let allTodosDetails = [...JSON.parse(localStorage.getItem("todosDetails"))];
            let indexOdThisTodo = allTodosDetails.findIndex(todoDetailsObject => {
                return todoDetailsObject.todoTitle === todoTitleInput
            });
                
                if(indexOdThisTodo !== -1){
                    let thisTodoDetails = allTodosDetails[indexOdThisTodo];

                    appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContent); 
                    let lastSelectedTodo = thisTodoDetails.todoTitle;
                    localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
                } else {
                    let thisTodoDetails = makeTodoObject(todoTitleInput, "");
                    allTodosDetails.push(thisTodoDetails);

                    localStorage.setItem("todosDetails", JSON.stringify(allTodosDetails))
                    appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContent);
                    let lastSelectedTodo = thisTodoDetails.todoTitle;
                    localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
                }

                

               

        } else {
            let allTodosDetails = [];
            let thisTodoDetails = makeTodoObject(todoTitleInput, "");
            allTodosDetails.push(thisTodoDetails);
            localStorage.setItem("todosDetails", JSON.stringify(allTodosDetails));
            appendTodoDetails(thisTodoDetails.todoTitle, thisTodoDetails.todoNotes, mainContent);
            let lastSelectedTodo = thisTodoDetails.todoTitle;
            localStorage.setItem("lastSelectedTodo", lastSelectedTodo);
            
        }
    });


    todoContainer.append(checkBox, todoTitle);

    parent.insertBefore(todoContainer, addTodoBtn);

}







