hideSideBar.addEventListener("click", () => {
    sideBar.classList.add("hide");
    
    mainContent.classList.add("extend")
});

sideBarMenu.addEventListener("click", () => {
    sideBar.classList.remove("hide");
    
    mainContent.classList.remove("extend")
});

projectNameInput.addEventListener("keydown", (event) => {

    if(event.key === "Escape"){
        overlay.classList.remove("show");
        projectPopUp.classList.remove("show");
        projectNameInput.value = "";    
    }

    if(event.key === "Enter" && projectNameInput.value.trim() !== ""){
        
        let newProject = makeProjectObjects(projectNameInput.value, []);
        projectsContainer.appendChild(handleTheProjectAndTodosDOM(newProject, projectsContainer, mainContent));
    
        const projs = [...JSON.parse(localStorage.getItem("projects"))];
        projs.push(newProject);
    
        localStorage.setItem("projects", JSON.stringify(projs))
    
        overlay.classList.remove("show");
        projectPopUp.classList.remove("show");
        projectNameInput.value = "";    

    }


});

saveProjectBtn.addEventListener("click", (e) => {
    if(projectNameInput.value.trim() === "") return;

    let newProject = makeProjectObjects(projectNameInput.value, []);
    projectsContainer.appendChild(handleTheProjectAndTodosDOM(newProject, projectsContainer, mainContent));

    const projs = [...JSON.parse(localStorage.getItem("projects"))];
    projs.push(newProject);

    localStorage.setItem("projects", JSON.stringify(projs))

    overlay.classList.remove("show");
    projectPopUp.classList.remove("show");
    projectNameInput.value = "";    
    


});



