window.onload = function(){

    if(localStorage.getItem("lastSelectedTodo")){
        let allTodosDetails = [...JSON.parse(localStorage.getItem("todosDetails"))];
        let lastSelectedTodo = localStorage.getItem("lastSelectedTodo");
        let lastSelectedTodoObj = allTodosDetails.find(todoDetails => {
            return todoDetails.todoTitle === lastSelectedTodo;
        });
        appendTodoDetails(lastSelectedTodoObj.todoTitle, lastSelectedTodoObj.todoNotes, mainContent);

    } else {
       
        if(localStorage.getItem("projects")){
            // console.log("in projects")
            let projs = [...JSON.parse(localStorage.getItem("projects"))];
            
          
            if(projs.length !== 0){
        
                let titleOfDefaultTodo = projs[0].todos[0];
                if(typeof titleOfDefaultTodo === "undefined") {
                    
                } else {
                    let defaultTodo = makeTodoObject(titleOfDefaultTodo, "");
                    appendTodoDetails(defaultTodo.todoTitle, defaultTodo.todoNotes, mainContent)
                }
            
              
               
            }

            
       
        }   
        
    }

    // if(localStorage.getItem("textAreaHeight")){
    //     const realTextAreaHeight = localStorage.getItem("textAreaHeight");
        
    //     textarea.style.height = realTextAreaHeight;
    // } else {
    //     textarea.style.height = textarea.scrollHeight + 'px';
    //     localStorage.setItem("textAreaHeight", textarea.style.height)
        
    // }


    if(localStorage.getItem("projects")){
       
        const projs = JSON.parse(localStorage.getItem("projects"));
        const projectss = [...projs]
       
        
        projectss.forEach((project) => {

        
        
        projectsContainer.appendChild(handleTheProjectAndTodosDOM(project, projectsContainer, mainContent));

        })

    } else {

        let projects = [];
        let defaultProject = {
            projectName: "For University",
            todos: ['Play some football', 'Studying', 'Have Some fun']
        }
        projects.push(defaultProject);
        
        localStorage.setItem("projects", JSON.stringify(projects));
        const projs = JSON.parse(localStorage.getItem("projects"));
        const projectss = [...projs]
        
        projectss.forEach((project) => {

        

        projectsContainer.appendChild(handleTheProjectAndTodosDOM(project, projectsContainer, mainContent));

        })
    }
    
}

